body {
  margin: 0;
  font-family: 'Abel', sans-serif;
}
.logos {
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  padding-left:5px;

}

.page{
  background-image: url(https://i.goopics.net/XgnJp.jpg);
  background-size: cover;  
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
}

.page-title{
  font-family: 'Abel', sans-serif;
  font-size: 30px;
  letter-spacing: 10px;
  margin: 10% 20% 10% 20%;
  text-align: center;
  background-color: #ffffff52;
  border-radius:20px;
  margin-top: 30%; 
}


.image-container{
  width: 100%;
  display: block;
}

.cta{
  border-radius: 30px;
  background-color: #ffbd4a;
  color: white;
  border:none;
  cursor: pointer;
  padding: 12px 16px;
  outline: none;
}
.load {
  position: fixed;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50);
          transform: translateX(-50);
}

a {
  text-decoration: none;
  color: black;
}

@media screen and (min-width: 500px){
  .logos{
    padding-left: 30px;
  }

  .page-title{
  margin-top: 10%;
  } 
}




 /*Style icon*/

 @font-face {
