/*Style Navigation*/
.menu_bar{
    display:block;
    font-size: 30px;
    position: fixed;
    right: 100%;
    z-index: 2;
}
.menu_bar span {
    font-size: 30px;
    color: black;
    position: fixed;
    top: 40px;
    right: 40px;
    z-index: 1;
}

.menu_bar_open span {
    color:white;
}
.menu_bar nav{
    opacity: 0;
    width: 100%;
    height:100%;
    position: fixed;
    top:5%;
    background-color: black;
    -webkit-transform: scale(.95);
            transform: scale(.95);
    -webkit-transition: all 280ms ease-in-out;
    transition: all 280ms ease-in-out;
    pointer-events: none;
}

.menu_bar_open nav {
   opacity: 1;
   -webkit-transform: scale(1);
           transform: scale(1);
   pointer-events: auto;
   top:0;


}

.menu_bar ul {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    padding-left:0px;
    list-style: none;
}

.menu_bar li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding:40px;
    
}

.menu_bar  a {
    color: white;
    text-decoration: none;
}



.menu_bar nav ul li a:hover {
    background: orange;
}

@media screen and (min-width: 500px){
    .menu_bar{
        right: 300px;
    }
    .menu_bar nav{
        width: 300px;
    }

}

